@import "https://fonts.googleapis.com/css2?family=Montserrat:wght@300;500;700&display=swap";
@keyframes animate {
  0% {
    background-position-x: 0;
  }

  100% {
    background-position-x: 2000px;
  }
}

@keyframes animate2 {
