footer {
  height: 1rem;
}

footer .wave {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 9rem;
  background: url(/images/wave.png);
  background-size: 1000px 100px;

  &.wave1 {
    animation: animate 30s linear infinite;
    z-index: 1000;
    opacity: 1;
    animation-delay: 0s;
    bottom: 0;
  }
  &.wave2 {
    animation: animate2 15s linear infinite;
    z-index: 999;
    opacity: 0.5;
    animation-delay: 0s;
    bottom: 1rem;
  }
  &.wave3 {
    animation: animate 30s linear infinite;
    z-index: 998;
    opacity: 0.2;
    animation-delay: -2s;
    bottom: 0;
  }
  &.wave4 {
    animation: animate2 15s linear infinite;
    z-index: 997;
    opacity: 0.7;
    animation-delay: -2s;
    bottom: 2rem;
  }
}
